<template>
  <div class="container">
    <ShoppingCart/>
    <h1 class="display-3">{{cur_item["Name"]}}</h1>
    <img v-for="picture,a,b in cur_item['Images'].split(',')" :src="picture" :key="b" height="350" width="350" />
    <br>
    <!-- <b-table striped hover :items="cur_item"></b-table> -->
    <b-button variant="outline-primary" @click="addToCart">Add to cart</b-button>
    <b-list-group>
      <b-list-group-item v-for="value, field, i in cur_item" :key="i">
        {{field}}:  {{value}}
      </b-list-group-item>
    </b-list-group>
  </div>
</template>

<script>
import data from "../../sample_shop_data.json";
import ShoppingCart from "./templates/ShoppingCart"

// const item = data.find(e => e.Name = this.$route.params.item)

export default {
  components: {
    ShoppingCart
  },
  data() {
    return {
      // cur_item: data.find(e => e.Name = this.$route.params.item)
    }
  },

  computed: {
    cur_item() {
      return data.find(e => e.SKU === this.$route.params.item)
    }
  },
  mounted() {
    console.log(this.$route.params.item)
    console.log("Current item: " + this.cur_item)
  },
  methods: {
    addToCart() {

      if (!this.$store.getters.getStatus) {
        this.$router.push("/login")
        return
      }

      let item = {}
      item[this.cur_item.SKU] = {q:1, p:this.cur_item["Sale price"]}
      console.log("Add to cart " + item)
      this.$store.commit("updateCart", item)
    }
  }
};
</script>

